import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import { useFormik } from 'formik';
import * as yup from 'yup';

import { DateTimePicker } from '@mui/x-date-pickers/DateTimePicker';
import { renderTimeViewClock } from '@mui/x-date-pickers/timeViewRenderers';
import TextField from '@mui/material/TextField';
import ListItemText from '@mui/material/ListItemText';
import Checkbox from '@mui/material/Checkbox';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import FormHelperText from '@mui/material/FormHelperText';
import Select from '@mui/material/Select';

import Loader from '../components/Loader';

import { LoadCreate } from '../api';
import { useSnackbar } from '../hooks';
import { CreateLoad, TruckTypes } from '../utils/dataTypes';

const validationSchema = yup.object({
  pickDate: yup
    .date()
    .typeError('Enter correct date')
    .required('Pick Date is required'),
  weight: yup.string().required('Weight is required'),
  truckType: yup
    .array(yup.string().oneOf(TruckTypes))
    .required('Truck Type is required'),
});

export default function CreateLoadForm() {
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const setSnackbarMessage = useSnackbar();

  const { mutate, isLoading } = useMutation(LoadCreate, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Created',
      });
      queryClient.invalidateQueries(['loads']);
      navigate(-1);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<CreateLoad>({
    initialValues: {
      pickDate: null,
      weight: '',
      truckType: [],
    },
    enableReinitialize: true,
    validationSchema: validationSchema,
    onSubmit: (values) => {
      mutate(values as unknown as CreateLoad);
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  return (
    <Box>
      <form onSubmit={formik.handleSubmit} onReset={formik.handleReset}>
        <Box sx={{ p: 3, display: 'grid' }}>
          <Box key="controls" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, width: '50%' }}>
              <Box key="weight" sx={{ height: '75px' }}>
                <TextField
                  fullWidth
                  size="small"
                  id="weight"
                  name="weight"
                  label="Weight"
                  value={formik.values.weight}
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  error={formik.touched.weight && Boolean(formik.errors.weight)}
                  helperText={formik.touched.weight && formik.errors.weight}
                />
              </Box>
              <Box key="truckType" sx={{ height: '75px' }}>
                <FormControl
                  fullWidth
                  size="small"
                  error={
                    formik.touched.truckType && Boolean(formik.errors.truckType)
                  }
                >
                  <InputLabel id="truckType">Truck Type</InputLabel>
                  <Select
                    labelId="truckType"
                    id="truckType"
                    name="truckType"
                    label="Truck Type"
                    multiple
                    value={formik.values.truckType}
                    renderValue={(selected) => selected.join(', ')}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                  >
                    {TruckTypes.map((item) => (
                      <MenuItem key={item} value={item}>
                        <Checkbox
                          checked={formik.values.truckType.indexOf(item) > -1}
                        />
                        <ListItemText primary={item} />
                      </MenuItem>
                    ))}
                  </Select>
                  <FormHelperText>
                    {formik.touched.truckType && formik.errors.truckType}
                  </FormHelperText>
                </FormControl>
              </Box>
              <Box key="pickDate" sx={{ height: '75px' }}>
                <DateTimePicker
                  label="Pick Date"
                  ampm={false}
                  disablePast
                  viewRenderers={{
                    hours: renderTimeViewClock,
                    minutes: renderTimeViewClock,
                    seconds: renderTimeViewClock,
                  }}
                  slotProps={{
                    textField: {
                      fullWidth: true,
                      size: 'small',
                      id: 'pickDate',
                      name: 'pickDate',
                      onBlur: formik.handleBlur,
                      error:
                        formik.touched.pickDate &&
                        Boolean(formik.errors.pickDate),
                      helperText:
                        formik.touched.pickDate && formik.errors.pickDate,
                      // onChange: (event) => console.log(event),
                    },
                  }}
                  value={formik.values.pickDate}
                  onChange={(value) =>
                    formik.setFieldValue('pickDate', value, true)
                  }
                  onClose={() => formik.setFieldTouched('pickDate', true, true)}
                />
              </Box>
            </Box>
            {/*<Box key="right" sx={{ p: 1, width: '50%', height: '75px' }}>*/}
            {/*  <Box key="phone" sx={{ height: '75px' }}>*/}
            {/*    <TextField*/}
            {/*      fullWidth*/}
            {/*      size="small"*/}
            {/*      id="phone"*/}
            {/*      name="phone"*/}
            {/*      label="Phone"*/}
            {/*      value={formik.values.phone || ''}*/}
            {/*      onChange={formik.handleChange}*/}
            {/*      onBlur={formik.handleBlur}*/}
            {/*      error={formik.touched.phone && Boolean(formik.errors.phone)}*/}
            {/*      helperText={formik.touched.phone && formik.errors.phone}*/}
            {/*    />*/}
            {/*  </Box>*/}
            {/*  <Box key="email" sx={{ height: '75px' }}>*/}
            {/*    <TextField*/}
            {/*      fullWidth*/}
            {/*      size="small"*/}
            {/*      id="email"*/}
            {/*      name="email"*/}
            {/*      label="Email"*/}
            {/*      value={formik.values.email}*/}
            {/*      onChange={formik.handleChange}*/}
            {/*      onBlur={formik.handleBlur}*/}
            {/*      error={formik.touched.email && Boolean(formik.errors.email)}*/}
            {/*      helperText={formik.touched.email && formik.errors.email}*/}
            {/*    />*/}
            {/*  </Box>*/}
            {/*  <Box key="password" sx={{ height: '75px' }}>*/}
            {/*    <TextField*/}
            {/*      fullWidth*/}
            {/*      size="small"*/}
            {/*      id="password"*/}
            {/*      name="password"*/}
            {/*      label="Password"*/}
            {/*      value={formik.values.password}*/}
            {/*      onChange={formik.handleChange}*/}
            {/*      onBlur={formik.handleBlur}*/}
            {/*      error={*/}
            {/*        formik.touched.password && Boolean(formik.errors.password)*/}
            {/*      }*/}
            {/*      helperText={formik.touched.password && formik.errors.password}*/}
            {/*    />*/}
            {/*  </Box>*/}
            {/*</Box>*/}
          </Box>
          <Box key="buttons" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="submit"
              >
                Create
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={isLoading} />
    </Box>
  );
}
